/* @import url('https://fonts.googleapis.com/css?family=Nunito+Sans'); */

.filters_hr {
  width: 50%;
  margin: 10px auto;
}

#filters_container {
  margin-bottom: 20px;
  background: #fff;
  border-radius: 5px;
  padding: 10px 10px 30px 10px;
}

.sub_filters_container {
  margin-bottom: 20px;
}

.filters_title {
  text-align: center;
  font-weight: 400;
}

#filters_list {
  display: grid;
  grid-template-columns: "1fr 1fr";
  grid-gap: "15px";
}

.filters_btns {
  background: #adc4ad;
  margin-bottom: 10px;
  color: #000;
  border: none;
  border-radius: 5px;
  padding: 5px 15px;
}

.filters_btns:hover {
  background: #adc4ad;
  color: #000;
}

.filters_btns:active,
.filters_btns_active {
  background: #209920 !important;
}

.card_img_container {
  text-align: center;
  background: #dfdfdf;
  margin: 20px;
}

.card_img_container>img {
  width: 200px;
  height: 200px;
}

footer {
  text-align: center;
}

#page_not_found_container {
  text-align: center;
}

#page_not_found_container img {
  width: 300px;
  height: 300px;
}

#page_not_found_container h1 {
  font-size: 100px;
}

#page_not_found_container span {
  /* font-family: "Nunito Sans"; */
  text-decoration: line-through;
  text-decoration-color: yellow;
}